footer {
  background: #f6eb50;
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 5rem;
}

footer a {
  color: var(--color-bg-variant);
}

.footer_logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.footer_socials {
  display: flex;
  justify-content: center;
  margin-bottom: 4rem;
}

.footer__socials a {
  background: var(--color-bg-variant);
  color: var(--color-white);
  padding: 0.6rem;
  border-radius: 0.6rem;
  display: flex;
  width: 45px;
  margin: auto;
  border: 1px solid transparent;
}

.footer__socials a:hover {
  background: transparent;
  color: var(--color-bg-variant);
  border-color: var(--color-bg-variant);
}

.footer__copyright {
  margin-bottom: 4rem;
  color: var(--color-bg-variant);
}

@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.5rem;
  }

  .footer__socials {
    margin-bottom: 2.6rem;
  }
}
